import React from 'react';

function Create({thumb, title, director, release, onDataChange, onCreate}) {
  return (
    <>
      <div className='vibe'>
        <h2>  / </h2>
        <dl>
          <dt></dt>
          <dd><input type='text' name="thumb" placeholder=' ' value={thumb} onChange={onDataChange} /></dd>
          <dt></dt>
          <dd><input type='text' name="title" placeholder='' value={title} onChange={onDataChange}  /></dd>
          <dt></dt>
          <dd><input type='text' name="director" placeholder='' value={director} onChange={onDataChange}  /></dd>
          <dt></dt>
          <dd><input type='text' name="release" placeholder='' value={release} onChange={onDataChange}  /></dd>
          <button type='submit' onClick={onCreate}></button>
        </dl>
      </div>
    </>
  );
}

export default Create;