import React, {useRef, useState} from 'react';
import Create from './Create';
import Product from './Product';
import dataProducts from '../data/data.json';
import {Swiper, SwiperSlide} from 'swiper/react';
import {Navigation, Scrollbar, Autoplay} from 'swiper';

import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import 'swiper/css/autoplay';

function ProductList() {
  const [products, setProducts] = useState(dataProducts.keys);
  
  const nextId = useRef(6);

  //    state .
  const [inputs, setInputs] = useState({
    thumb: '',
    title: '',
    director: '',
    release: ''
  });

  //    state   
  const {thumb, title, director, release} = inputs;

  // onDataChange 
  const onDataChange=(e)=>{
    const{name, value}=e.target;
     // state 
    setInputs({
      ...inputs, //        
      [name]:value //      []
    });
  }

  //        
  const onCreate=()=>{
    const product={
      id: 'No.'+nextId.current,
      thumb,
      title,
      director,
      release
    }
    setProducts([...products, product]);
    setInputs({
      thumb: '',
      title: '',
      director: '',
      release: ''
    });
    nextId.current+=1;
  }
  //  
  const onRemove = id =>{
    setProducts(products.filter(product=> product.id!==id))
  };
  return (
    <>
      <Swiper
        modules={[Navigation, Scrollbar, Autoplay]} className="mySwiper"

        spaceBetween={50}
        centeredSlides={true}
        autoplay={{
          delay:3000,  
          disableOnInteraction: false,
        }}
        navigation={true}
        slidesPerView={1}

        loop={true}

        scrollbar={{draggable:true}}
        >
      
        <SwiperSlide><span className='text_plus'>[]	   (1/16  )</span></SwiperSlide>
        <SwiperSlide><span className='text_plus'>[]	2023 2   </span></SwiperSlide>
        <SwiperSlide><span className='text_plus'>[]	[CGV]   (4 3)</span></SwiperSlide>
        <SwiperSlide><span className='text_plus'>[] [CGV] 4/2() &#x003C;2023 &#x003E;     </span></SwiperSlide>
        <SwiperSlide><span className='text_plus'>[/]  &#x003C;  &#x003E;  ()  </span></SwiperSlide>
      </Swiper>

      <Create onDataChange={onDataChange} thumb={thumb} title={title} director={director} release={release} onCreate={onCreate} />
      <ul className='movie_list'>
        {products.map((product=><Product product={product} key={product.id} onRemove={onRemove}/>))}
      </ul>
    </>
  );
}

export default ProductList;